<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 列表卡片 -->
    <el-card class="box-card">
      <el-button type="primary" @click="addCateDialogShow">添加分类</el-button>
      <el-table
        :data="cateList"
        style="width: 100%"
        row-key="cat_id"
        border
        :tree-props="{ children: 'children', haschildren: 'haschildren' }"
      >
        <el-table-column
          type="index"
          label="#"
          :index="indexMethod"
          width="80px"
          align="center"
        >
          <template v-slot="data">
            {{ data.row.index }}
          </template>
        </el-table-column>
        <el-table-column prop="cat_name" label="分类名称"> </el-table-column>
        <el-table-column label="是否有效">
          <template v-slot="data">
            <i
              class="el-icon-success"
              v-if="data.row.cat_deleted == false"
              style="color: lightGreen"
            ></i>
            <i class="el-icon-error" v-else style="color: lightRed"></i>
          </template>
        </el-table-column>
        <el-table-column label="排序">
          <template v-slot="data">
            <el-tag type="primary" v-if="data.row.cat_level == 0">一级</el-tag>
            <el-tag type="success" v-else-if="data.row.cat_level == 1"
              >二级</el-tag
            >
            <el-tag type="warning" v-else>三级</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template v-slot="data">
            <el-button type="primary" icon="el-icon-edit" @click="editCateDialogShow(data.row.cat_id,data.row.cat_name)">编辑</el-button>
            <el-button type="danger" icon="el-icon-delete" @click="deleteCate(data.row.cat_id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 8, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!-- 添加分类对话框 -->
    <el-dialog
      title="添加分类"
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="addCateDialogClose"
    >
      <el-form
        ref="addCateFormRef"
        :model="addCateForm"
        :rules="addCateFormRules"
        label-width="80px"
      >
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类">
          <!-- options绑定的数据 -->
          <!-- props配置数据 -->
          <el-cascader
            v-model="cascaderSelectedKey"
            :options="parentCateList"
            :props="cascaderProps"
            @change="cascaderHandleChange"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCate"
          >确 定</el-button
        >
      </span>
    </el-dialog>

    <!-- 编辑分类对话框 -->
    <el-dialog
      title="编辑分类"
      :visible.sync="editCateDialogVisible"
      width="50%"
      @close="editCateDialogClose"
    >
      <el-form
        ref="editCateFormRef"
        :model="editCateForm"
        :rules="editCateFormRules"
        label-width="80px"
      >
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="editCateForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editCate"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Cate',
  data() {
    return {
      // 请求分类列表数据参数
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 分类列表数据
      cateList: [],
      total: 0,
      addCateDialogVisible: false,
      addCateForm: {
        cat_name: '',
        cat_pid: 0,
        cat_level: 0
      },
      addCateFormRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      },
      // 父级分类列表数据
      parentCateList: [],
      // 级联选择器配置项
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        checkStrictly: true
      },
      // 级联选择器选中所返回的由配置中value组成的数组
      cascaderSelectedKey: [],
      editCateForm: {
        cat_id: 0,
        cat_name: ''
      },
      editCateDialogVisible: false,
      editCateFormRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 获取三级商品分类列表数据
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品分类列表失败')
      }
      this.cateList = res.data.result
      this.setCateListIndex(this.cateList)
      this.total = res.data.total
    },
    // 设置分类表索引列为空
    indexMethod(index) {
      // eslint-disable-next-line no-return-assign
      return (index = '')
    },
    // 设置每一行的索引
    setCateListIndex(param) {
      if (param === this.cateList) {
        param.forEach((item, index) => {
          item.index = index + 1
          if (item.children) {
            return this.setCateListIndex(item)
          }
        })
      } else {
        param.children.forEach((item, index) => {
          // eslint-disable-next-line prefer-const
          let newIndex = index + 1
          item.index = param.index + '-' + newIndex
          if (item.children) {
            return this.setCateListIndex(item)
          }
        })
      }
    },
    // 每页显示多少条数据变化事件 pagesize
    handleSizeChange(newPagesize) {
      this.queryInfo.pagesize = newPagesize
      this.getCateList()
    },
    // 当前显示第几页变化事件 pagenum
    handleCurrentChange(newPagenum) {
      this.queryInfo.pagenum = newPagenum
      this.getCateList()
    },
    addCateDialogShow() {
      // 获取父级分类数据
      this.getParentCateList()
      // 弹出添加分类对话框
      this.addCateDialogVisible = true
    },
    // 获取父级分类数据
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品分类列表失败')
      }
      this.parentCateList = res.data
    },
    // 级联选择器变化事件
    cascaderHandleChange() {
      if (this.cascaderSelectedKey.length > 0) {
        // 级联选择器选中的父级ID
        this.addCateForm.cat_pid = this.cascaderSelectedKey[this.cascaderSelectedKey.length - 1]
        // 级联选择器选中后所要修改分类的级别
        this.addCateForm.cat_level = this.cascaderSelectedKey.length
      } else {
        // 重置级联选择器数据
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    // 添加分类对话框关闭事件
    addCateDialogClose() {
      // 重置输入框数据
      this.$refs.addCateFormRef.resetFields()
      // 重置级联选择器数据
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
      this.cascaderSelectedKey = []
    },
    // 点击确定按钮添加分类
    async addCate() {
      // 预验证
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) return this.$message.error('添加分类失败')
        // 提示添加成功
        this.$message.success('添加分类成功')
        // 关闭添加分类对话框
        this.addCateDialogVisible = false
        // 重新获取分类列表数据
        this.getCateList()
      })
    },
    async editCateDialogShow(id, name) {
      this.editCateForm.cat_id = id
      this.editCateForm.cat_name = name
      this.editCateDialogVisible = true
    },
    // 编辑对话框关闭事件
    editCateDialogClose() {
      this.$refs.editCateFormRef.resetFields()
    },
    // 编辑分类提交
    async editCate() {
      // 预验证
      this.$refs.editCateFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put(`categories/${this.editCateForm.cat_id}`, { cat_name: this.editCateForm.cat_name })
        if (res.meta.status !== 200) return this.$message.error('更新分类名称失败')
        // 提示更新分类名称成功
        this.$message.success('更新分类名称成功')
        // 关闭更新分类名称对话框
        this.editCateDialogVisible = false
        // 重新获取分类列表数据
        this.getCateList()
      })
    },
    // 删除分类
    async deleteCate(id) {
      const confirmResult = await this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)

      if (confirmResult !== 'confirm') return this.$message.info('已取消删除')
      const { data: res } = await this.$http.delete(`categories/${id}`)
      if (res.meta.status !== 200) return this.$message.error('删除分类失败')
      // 提示删除分类成功
      this.$message.success('删除分类成功')
      // 重新获取分类列表数据
      this.getCateList()
    }
  }
}
</script>

<style lang="less" scoped>
  .el-cascader {
    width: 100%;
  }
</style>
