<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>参数列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 列表卡片 -->
    <el-card class="box-card">
      <!-- 注意点 -->
      <el-alert
        title="注意：只允许为第三级分类设置相关参数！"
        type="warning"
        show-icon
        :closable="false"
      >
      </el-alert>

      <!-- 选择商品分类 -->
      <el-row class="marTop">
        <el-col>
          <span>选择商品分类：</span>
          <el-cascader
            v-model="selectedKeys"
            :options="cateList"
            :props="cascaderProps"
            @change="handleChange"
            clearable
          ></el-cascader>
        </el-col>
      </el-row>

      <!-- 标签页 -->
      <el-tabs v-model="activeName" @tab-click="handleTabsClick">
        <!-- 动态参数面板 -->
        <el-tab-pane label="动态参数" name="many">
          <el-button
            type="primary"
            :disabled="isBtnDisable"
            @click="addDialogShow"
            >添加动态参数</el-button
          >
          <!-- 参数表格 -->
          <el-table :data="manyList" border stripe>
            <el-table-column type="expand">
              <template v-slot="data">
                <!-- tag展示 -->
                <el-tag
                  :key="i"
                  v-for="(item, i) in data.row.attr_vals"
                  closable
                  :disable-transitions="false"
                  @close="tagHandleClose(i,data.row)"
                >
                  {{ item }}
                </el-tag>

                <!-- 添加按钮 -->
                <el-input
                  class="input-new-tag"
                  v-if="inputVisible"
                  v-model="newValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(data.row)"
                  @blur="handleInputConfirm(data.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column
              prop="attr_name"
              label="参数名称"
            ></el-table-column>
            <el-table-column label="操作">
              <template v-slot="data">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  @click="editDialogShow(data.row)"
                  >编辑</el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  @click="deleteParam(data.row.attr_id)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>

        <!-- 静态属性面板 -->
        <el-tab-pane label="静态属性" name="only">
          <el-button
            type="primary"
            :disabled="isBtnDisable"
            @click="addDialogShow"
            >添加静态属性</el-button
          >
          <!-- 属性表格 -->
          <el-table :data="onlyList" border stripe>
            <el-table-column type="expand">
              <template v-slot="data">
                <!-- tag展示 -->
                <el-tag
                  :key="i"
                  v-for="(item, i) in data.row.attr_vals"
                  closable
                  :disable-transitions="false"
                  @close="tagHandleClose(i,data.row)"
                >
                  {{ item }}
                </el-tag>

                <!-- 添加按钮 -->
                <el-input
                  class="input-new-tag"
                  v-if="inputVisible"
                  v-model="newValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(data.row)"
                  @blur="handleInputConfirm(data.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column
              prop="attr_name"
              label="参数名称"
            ></el-table-column>
            <el-table-column label="操作">
              <template v-slot="data">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  @click="editDialogShow(data.row)"
                  >编辑</el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  @click="deleteParam(data.row.attr_id)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!-- 添加和编辑参数对话框 -->
    <el-dialog
      :title="titleText"
      :visible.sync="DialogVisible"
      width="30%"
      @close="DialogClose"
    >
      <el-form
        :model="DialogForm"
        ref="DialogFormRef"
        :rules="DialogFormRules"
        label-width="80px"
      >
        <!-- 添加框内容 -->
        <el-form-item
          prop="attr_name"
          :label="DialogFormLabel"
          v-if="toggleDialog == 'add'"
        >
          <el-input v-model="DialogForm.attr_name"></el-input>
        </el-form-item>

        <!-- 编辑框内容 -->
        <el-form-item
          prop="attr_name"
          label="参数名称"
          v-if="toggleDialog == 'edit'"
        >
          <el-input v-model="DialogForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="DialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="operateParam">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// 导入Vuex的辅助函数
import { mapMutations, mapState, mapGetters } from 'vuex'

export default {
  name: 'Params',
  created() {
    this.getCateList()
    // 重置vuex中selectedArray的值和表格manyList onlyList的值
    this.handleChange(this.selectedKeys)
  },
  data() {
    return {
      selectedKeys: [],
      cateList: [],
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 默认显示标签页
      activeName: 'many',
      DialogVisible: false,
      toggleDialog: '',
      DialogForm: {
        attr_name: '',
        attr_vals: ''
      },
      DialogFormRules: {
        attr_name: [
          { required: true, message: '请输入参数名称', trigger: 'blur' }
        ]
      },
      newValue: '',
      inputVisible: false
    }
  },
  methods: {
    // Vuex映射的方法
    ...mapMutations(['getCateParamsVuex', 'getRolesList', 'handleChangeVuex', 'resetSelectedArray']),
    // 获取三级分类列表数据
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 3 }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取分类列表数据失败')
      }
      this.cateList = res.data
    },
    // 级联选择器变化事件
    handleChange() {
      // vuex映射的方法
      this.handleChangeVuex({ selectedKeys: this.selectedKeys })
    },
    // 获取分类参数
    getCateParams(paramTypes) {
      // vuex映射过来的方法
      this.getCateParamsVuex(paramTypes)
    },
    handleTabsClick() {},
    // 添加参数对话框关闭事件
    DialogClose() {
      // 重置表单数据
      this.$refs.DialogFormRef.resetFields()
    },
    // 添加对话框弹出控制
    addDialogShow() {
      this.DialogForm = {
        attr_name: ''
      }
      this.toggleDialog = 'add'
      this.DialogVisible = true
    },
    // 添加和编辑参数
    async operateParam() {
      // 预验证
      this.$refs.DialogFormRef.validate(async valid => {
        if (!valid) return
        // 发送请求添加数据
        if (this.toggleDialog === 'add') {
          const { data: res } = await this.$http.post(
            `categories/${this.cateId}/attributes`,
            {
              attr_name: this.DialogForm.attr_name,
              attr_sel: this.activeName
            }
          )
          if (res.meta.status !== 201) {
            return this.$message.error('添加参数失败')
          }
          // 提示添加成功
          this.$message.success('添加参数成功')
          // 重新获取参数数据
          this.getCateParams(this.activeName)
          // 关闭对话框
          this.DialogVisible = false
        } else {
          // 发送请求编辑数据
          const { data: res } = await this.$http.put(
            `categories/${this.cateId}/attributes/${this.DialogForm.attr_id}`,
            {
              attr_name: this.DialogForm.attr_name,
              attr_sel: this.activeName,
              attr_vals: this.DialogForm.attr_vals
            }
          )
          if (res.meta.status !== 200) this.$message.error('更新参数失败')
          // 提示成功
          this.$message.success('更新参数成功')
          // 重新获取参数列表数据
          this.getCateParams(this.activeName)
          // 关闭对话框
          this.DialogVisible = false
        }
      })
    },
    // 编辑对话框弹出控制
    async editDialogShow(row) {
      // 将行vals转为字符串存储
      this.DialogForm.attr_vals = row.attr_vals.join(' ')
      this.toggleDialog = 'edit'
      // 根据ID查询分类
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes/${row.attr_id}`
      )
      if (res.meta.status !== 200) return this.$message.error('查询分类失败')
      this.DialogForm = res.data
      this.DialogVisible = true
    },
    // 删除对应ID的参数
    async deleteParam(id) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除该参数, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') return this.$message.info('已取消删除')
      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${id}`
      )
      if (res.meta.status !== 200) this.$message.error('删除参数失败')
      // 提示成功
      this.$message.success('删除参数成功')
      // 重新获取参数列表数据
      this.getCateParams(this.activeName)
    },
    async setAttrVals(param) {
      // 转换为字符串
      this.DialogForm.attr_vals = param.attr_vals.join(' ')
      const { data: res } = await this.$http.put(
            `categories/${this.cateId}/attributes/${param.attr_id}`,
            {
              attr_name: param.attr_name,
              attr_sel: this.activeName,
              attr_vals: this.DialogForm.attr_vals
            }
      )
      if (res.meta.status !== 200) this.$message.error('更新参数属性失败')
    },
    // tag删除事件
    tagHandleClose(index, param) {
      // 从attr_vals中删除选中的数据
      param.attr_vals.splice(index, 1)
      this.setAttrVals(param)
    },
    // 添加value输入框 按下enter  blur触发事件
    handleInputConfirm(param) {
      if (this.newValue.trim() !== '') {
        // 发送请求将新数据添加到attr_vals
        param.attr_vals.push(this.newValue)
        this.setAttrVals(param)
      }
      // 关闭输入框
      this.inputVisible = false
      // 清空输入框数据
      this.newValue = ''
    },
    // 点击new tag按钮显示输入框
    showInput() {
      this.inputVisible = true
    }
  },
  computed: {
    // vuex映射state数据
    ...mapState(['manyList', 'onlyList']),
    // vuex映射的getter计算属性
    ...mapGetters(['cateId', 'isBtnDisable']),
    // 动态框title控制
    titleText() {
      if (this.toggleDialog === 'add') {
        return this.activeName === 'many' ? '添加动态参数' : '添加静态属性'
      } else {
        return this.activeName === 'many' ? '编辑动态参数' : '编辑静态属性'
      }
    },
    // 添加动态框输入框名称控制
    DialogFormLabel() {
      if (this.activeName === 'many') {
        return '动态参数'
      } else {
        return '静态属性'
      }
    }
  }
}
</script>

<style lang="less" scoped>
.marTop {
  margin-top: 15px;
}
.input-new-tag {
  width: 150px;
}
</style>
